
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

import { mapActions } from 'vuex'
export default {
name: "creditCard",
data() {
return {
users:localStorage.getItem('users')?JSON.parse(localStorage.getItem('users')):''
}

},
props:['creditCard','myheight'],
components: {
Swipeout,SwipeoutItem,SwipeoutButton
},
created() {
console.log(this.$props)

},
mounted() {

},
methods: {
onButtonClick(id){
const _this = this;
const users = this.users;
console.log(this.users);
this.$vux.confirm.show({
title: '确认删除？',
onCancel () {
},
onConfirm () {
_this.$axios.post('/api/Partner/delCredit',`data=${JSON.stringify({id})}`)
.then(res=>{
mui.toast(res.msg)
if(res.code==1){
_this.getCreditCard()
}
})
}
})
},
...mapActions([
'getCreditCard'
])
}
}



